.alert {
  position: fixed;
  z-index: 40;
  top: 10px;
  right: 10px;
  min-width: 250px;
  min-height: 70px;
  padding: 10px;
  margin: 10px;
  line-height: 1.8;
  cursor: pointer;
  font-family: sans-serif;
  font-weight: 400;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #f4f5f6;
  border: none;
  border-radius: 25px;
  box-sizing: border-box;
}

.alertText {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  font-size: 16px;
}

.alertClose {
  padding: 0 10px 0 10px;
  font-size: 20px;
  width: 40px;
  height: 100%;
  border: none;
  background: transparent;
  border-left: 1px solid black;
}

